import styles from "./Menubar.module.scss";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleDown } from "@fortawesome/free-solid-svg-icons";
import { faCircle } from "@fortawesome/free-solid-svg-icons";
import { Fragment, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import axios from "axios";
import { getCookie } from "../../cookie";
import { SV_HOST } from "../../constants";

const Menubar = () => {
  const [login, setLogin] = useState("");
  const [loginSelect, setLoginSelect] = useState(false);

  const [signup, setSignup] = useState("");
  const [signupSelect, setSignupSelect] = useState(false);
  const [leftMenu, setLeftMenu] = useState(["", "", ""]);
  const [rightMenu, setRightMenu] = useState(["", ""]);
  const [isLogin, setIsLogin] = useState(true);
  const [isMentor, setIsMentor] = useState(true);
  const [subMenu, setSubMenu] = useState("");
  const [leftLink, setLeftLink] = useState([
    "http://localhost:3000",
    "http://localhost:3000",
    "http://localhost:3000",
  ]);
  const [rightLink, setRightLink] = useState([
    "http://localhost:3000",
    "http://localhost:3000",
  ]);
  const [subSelect, setSubSelect] = useState(false);
  const toggleLogin = () => {
    setLoginSelect((current) => !current);
    setSignupSelect(false);
  };

  const toggleSignup = () => {
    setSignupSelect((current) => !current);
    setLoginSelect(false);
  };

  const toggleSubSelect = () => {
    setSubSelect((current) => !current);
  };
  useEffect(() => {
    if (!isLogin) {
      setLeftMenu(["", "", ""]);
      setRightMenu([]);
      setLeftLink([
        "http://localhost:3000",
        "http://localhost:3000",
        "http://localhost:3000",
      ]);
    } else {
      if (isMentor) {
        setLeftMenu(["", "", "", ""]);
        setRightMenu(["", ""]);
        setLeftLink([
          "http://localhost:3000/mentor",
          "http://localhost:3000/mentor/consult",
          "http://localhost:3000/mentor",
          "http://localhost:3000/community",
        ]);
        setRightLink([
          "http://localhost:3000/mentor",
          "http://localhost:3000/mentor",
        ]);
      } else {
        setLeftMenu(["", "", "", "", ""]);
        setRightMenu(["", " "]);
        setLeftLink([
          "http://localhost:3000/mentee",
          "http://localhost:3000/mentee/mentor",
          "http://localhost:3000/mentee",
          "http://localhost:3000/mentee",
          "http://localhost:3000/community",
        ]);
        setRightLink([
          "http://localhost:3000/mentee",
          "http://localhost:3000/mentee",
        ]);
      }
    }
  }, [isLogin, isMentor]);

  const test = () => {
    console.log(getCookie("jwtToken"));
    axios
      .get(`${SV_HOST}/test/jwt`, {
        headers: {
          "Content-Type": "application/json",
          "ngrok-skip-browser-warning": "69420",
          Authorization: `Bearer ${getCookie("jwtToken")}`,
        },
      })
      // post   payload empty    empty {} .
      // ngrok get method  cors  , ngrok  get method   ngork html   .
      //      headers 'ngrok-skip-browser-warning': '69420' .
      //  : https://velog.io/@jy3026/ngrok-CORS-%ED%95%B4%EA%B2%B02
      // .post(`https://1c05-183-107-1-194.ngrok-free.app/test/jwt`, {}, {
      //   headers: {
      //     "Content-Type": "application/json",
      //     'ngrok-skip-browser-warning': '69420',
      //     Authorization: `Bearer ${getCookie("jwtToken")}`,
      //   },
      // })
      .then((res) => {
        console.log(res);
        window.alert("success");
      })
      .catch((err) => {
        console.log(err);
        window.alert("error");
      });
  };
  useEffect(() => {});
  return (
    <>
      <div className={styles.Menubar}>
        <div className={styles.MenubarLeft}>
          <div
            className={styles.Logo}
            onClick={
              // setIsMentor((current) => !current);
              test
            }
          >
            CAREER
          </div>
          {/* <button
            onClick={() => {
              setIsLogin((current) => !current);
            }}
          >
            {isLogin ? "" : ""}
          </button> */}
          {leftMenu.map((menu, i) => {
            return (
              <Link to={leftLink[i]} className={styles.MenubarSpan} key={i}>
                {menu}
              </Link>
            );
          })}
        </div>
        <div className={styles.MenubarRight}>
          {isLogin ? (
            <>
              {rightMenu.map((menu, i) => {
                return (
                  <Fragment key={i}>
                    <div className={[styles.CircleIcon, styles.Icon].join(" ")}>
                      <FontAwesomeIcon icon={faCircle} />
                    </div>
                    <Link to={rightLink[i]} className={styles.MenubarSpan}>
                      {menu}
                    </Link>
                  </Fragment>
                );
              })}

              <img
                alt=""
                src="https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_1280.png"
              />
              <div className={styles.MenubarSpan}>
                {isMentor ? "" : ""}
                <span></span>
              </div>
              <div className={styles.Icon}>
                <FontAwesomeIcon onClick={toggleSubSelect} icon={faAngleDown} />
              </div>
              {subSelect ? (
                <div className={[styles.Popup, styles.SignPopup].join(" ")}>
                  <span
                    onClick={() => {
                      setSubMenu("");
                      setSubSelect(false);
                    }}
                  >
                    <Link
                      to={
                        isMentor
                          ? `http://localhost:3000/mentor/setting`
                          : `http://localhost:3000/mentee/setting`
                      }
                      style={
                        subMenu === ""
                          ? {
                              color: "#3B71B9",
                              fontWeight: "800",
                              textDecoration: "none",
                            }
                          : { color: "#2F5383", textDecoration: "none" }
                      }
                    >
                      
                    </Link>
                  </span>
                  <hr />
                  <span
                    onClick={() => {
                      setSubMenu(" ");
                      setSubSelect(false);
                    }}
                  >
                    <Link
                      to={
                        isMentor
                          ? `http://localhost:3000/mentor/profile`
                          : `http://localhost:3000/mentee/profile`
                      }
                      style={
                        subMenu === " "
                          ? {
                              color: "#3B71B9",
                              fontWeight: "800",
                              textDecoration: "none",
                            }
                          : { color: "#2F5383", textDecoration: "none" }
                      }
                    >
                       
                    </Link>
                  </span>
                  <hr />
                  <span
                    onClick={() => {
                      setSubMenu("");
                      setSubSelect(false);
                      setIsLogin(false);
                    }}
                  >
                    <Link
                      to={`http://localhost:3000/`}
                      style={{ color: "#2F5383", textDecoration: "none" }}
                    >
                      
                    </Link>
                  </span>
                </div>
              ) : null}
            </>
          ) : (
            <>
              <div className={[styles.CircleIcon, styles.Icon].join(" ")}>
                <FontAwesomeIcon icon={faCircle} />
              </div>
              <div className={styles.MenubarSpan}>{login}</div>
              <div className={styles.Icon}>
                <FontAwesomeIcon onClick={toggleLogin} icon={faAngleDown} />
              </div>
              {loginSelect ? (
                <div className={styles.Popup}>
                  <span
                    onClick={() => {
                      setLogin(" ");
                      toggleLogin();
                      setSignup("");
                    }}
                  >
                    <Link
                      to={`http://localhost:3000/loginStudent`}
                      style={
                        login === " "
                          ? {
                              color: "#3B71B9",
                              fontWeight: "800",
                              textDecoration: "none",
                            }
                          : { color: "#2F5383", textDecoration: "none" }
                      }
                    >
                       
                    </Link>
                  </span>
                  <hr />
                  <span
                    style={
                      login === " "
                        ? { color: "#3B71B9", fontWeight: "800" }
                        : { color: "#2F5383" }
                    }
                    onClick={() => {
                      setLogin(" ");
                      toggleLogin();
                      setSignup("");
                    }}
                  >
                     
                  </span>
                </div>
              ) : null}
              <div className={styles.MenubarSpan}>{signup}</div>
              <div className={styles.Icon}>
                <FontAwesomeIcon onClick={toggleSignup} icon={faAngleDown} />
              </div>
              {signupSelect ? (
                <div className={[styles.Popup, styles.SignPopup].join(" ")}>
                  <span
                    onClick={() => {
                      setSignup(" ");
                      toggleSignup();
                      setLogin("");
                    }}
                  >
                    <Link
                      to={`http://localhost:3000/signMentor`}
                      style={
                        signup === " "
                          ? {
                              color: "#3B71B9",
                              fontWeight: "800",
                              textDecoration: "none",
                            }
                          : { color: "#2F5383", textDecoration: "none" }
                      }
                    >
                       
                    </Link>
                  </span>
                  <hr />
                  <span
                    style={
                      login === " "
                        ? {
                            color: "#3B71B9",
                            fontWeight: "800",
                            textDecoration: "none",
                          }
                        : { color: "#2F5383", textDecoration: "none" }
                    }
                    onClick={() => {
                      setSignup(" ");
                      toggleSignup();
                      setLogin("");
                    }}
                  >
                    <Link
                      to={`http://localhost:3000/signMentee`}
                      style={
                        signup === " "
                          ? {
                              color: "#3B71B9",
                              fontWeight: "800",
                              textDecoration: "none",
                            }
                          : { color: "#2F5383", textDecoration: "none" }
                      }
                    >
                       
                    </Link>
                  </span>
                </div>
              ) : null}
            </>
          )}
        </div>
      </div>
      <hr className={styles.Line} />
    </>
  );
};

export default Menubar;
